	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"New Digs","date":"01 May 2003","slug":"/2003-05-01-new-digs/","tags":"Y2003"},"html":"<p>This afternoon PapaScott moved to its own virtual server, running on FreeBSD and hosted by <a href=\"http://www.escapebox.net\">EscapeBox</a> in Berlin. The DNS change will have propagated by noon tomorrow, so until then you might get redirected to the new IP address. </p>\n<p>I'd been considering for several weeks now whether to move PapaScott to a dedicated or virtual server. Not that I need it. The site is pretty low tech and low volume, and doesn't require any great amounts of CPU or bandwidth. I just wanted to be root! I set up web servers on FreeBSD and Linux for a living, and I wanted to be able to set up my web server the way I want to, install the latest PHP if I feel like it, or try out Tomcat. And to able to break things, although I don't expect to break anything.</p>\n<p>Inspired by <a href=\"http://weblog.burningbird.net/fires/001126.htm\">Burningbird's</a> <a hef=\"http://weblog.burningbird.net/fires/001131.htm\">quest</a> for a <a href=\"http://weblog.burningbird.net/fires/001138.htm\">new server</a>, I started searching around, discovered a couple of hosters that have excellent reviews, and made my choice.</p>\n<p>I still highly recommend <a href=\"http://www.cornerhost.com\">Cornerhost</a> as a hoster for anyone who does not want or need root.</p>"},"next":{"fields":{"title":"Bush Regime Playing Cards","date":"01 May 2003","slug":"/2003-05-01-bush-regime-playing-cards/"}},"previous":{"fields":{"title":"Trophy Husband","date":"01 May 2003","slug":"/2003-05-01-trophy-husband/"}}},{"node":{"fields":{"title":"Bush Regime Playing Cards","date":"01 May 2003","slug":"/2003-05-01-bush-regime-playing-cards/","tags":"Y2003"},"html":"<p><a title=\"hold 'em or fold 'em?\" href=\"http://uggabugga.blogspot.com/2003_04_20_uggabugga_archive.html\">uggabugga: Bush Regime Playing Cards</a></p>"},"next":{"fields":{"title":"Apple Music Store: The New 8-Track Tape","date":"01 May 2003","slug":"/2003-05-01-apple-music-store-the-new-8-track-tape/"}},"previous":{"fields":{"title":"New Digs","date":"01 May 2003","slug":"/2003-05-01-new-digs/"}}},{"node":{"fields":{"title":"Apple Music Store: The New 8-Track Tape","date":"01 May 2003","slug":"/2003-05-01-apple-music-store-the-new-8-track-tape/","tags":"Y2003"},"html":"<p><a title=\"But even an 8-track tape would work on more than 3 tape players\" href=\"http://www.macobserver.com/columns/devilsadvocate/2003/20030430.shtml\">The Mac Observer: Apple Music Store: The New 8-Track Tape</a></p>"},"next":{"fields":{"title":"Alabama Legislators Render State's Obscenity Law Illegal","date":"01 May 2003","slug":"/2003-05-01-alabama-legislators-render-states-obscenity-law-illegal/"}},"previous":{"fields":{"title":"Bush Regime Playing Cards","date":"01 May 2003","slug":"/2003-05-01-bush-regime-playing-cards/"}}}],"pathPrefix":"","first":false,"last":false,"index":759,"pageCount":1101}}

